class LeminCroppedCaptcha {
    constructor(containerId, captchaId) {
        this.containerId = containerId;
        this.captchaId = captchaId;
    }

    getCaptchaValue() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).getCaptchaValue()
    }

    adjustSize() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).adjustSize()
    }
    execute() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).execute()
    }
    destroyCaptcha() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).destroyCaptcha()
    }

    display() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).display()
    }

    getToken() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).getToken()
    }

    isReady() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).isReady()
    }

    loadPuzzleError(callback) {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).loadPuzzleError(callback)
    }

    loadPuzzleImage() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).loadPuzzleImage()
    }

    onLoad(callback) {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).onLoad(callback)
    }

    onVerify(callback) {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).onLoad(callback)
    }

    reloadPuzzle() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).reloadPuzzle()
    }

    resetCaptcha() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).resetCaptcha()
    }

    setUserinfo() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).setUserinfo()
    }

    stopRefreshing() {
        return window.leminCroppedCaptcha.getCaptcha(this.captchaId).stopRefreshing()
    }
}

module.exports = LeminCroppedCaptcha
