const React = require('react');

function LeminCroppedCaptchaContainer({captchaId, containerId, src, onVerify}) {
    React.useEffect(() => {
        if (!src && !captchaId) {
            throw new Error('Src or Captchaid is not defined')
        }
        const regex = /CROPPED_\w+/g;
        const captchaContainer = document.getElementById(containerId);
        const leminScript = document.createElement('script');
        const scriptUrl = src || `https://api.leminnow.com/captcha/v1/cropped/${captchaId}/js`;
        leminScript.onload = () => {
            setTimeout(() => {
                const captcha = window.leminCroppedCaptcha.getCaptcha(captchaId ? captchaId : src && src.match(regex));
                captcha.onVerify && captcha.onVerify((isVerified) => {
                    onVerify && onVerify(isVerified)
                })
            })
        }
        leminScript.setAttribute('src', scriptUrl);
        if (captchaContainer && captchaContainer.parentNode) {
            captchaContainer.parentNode.appendChild(leminScript);
        }
    }, [])

    return React.createElement('div', {id: containerId});
}


module.exports = LeminCroppedCaptchaContainer
